body {
	margin: 0;
	padding: 0;
	font-family: "Courier New", 'Courier New', Courier, monospace;
	/* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif; */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: #7b4397; /* fallback for old browsers */
	background: -webkit-linear-gradient(89deg, #dc2430 0%, #7b4397 100%); /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-linear-gradient(
		1deg,
		#dc2430 0%,
		#7b4397 100%
	);
	background: linear-gradient(
		89deg,
		#dc2430 0%,
		#7b4397 100%
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

button {
	cursor: pointer;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.Tilt {
	background: #7b4397; /* fallback for old browsers */
	background: -webkit-linear-gradient(180deg, #dc2430 0%, #7b4397 100%); /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(#dc2430),
		to(#7b4397)
	);
	background: -webkit-linear-gradient(
		top,
		#dc2430 0%,
		#7b4397 100%
	);
	background: linear-gradient(
		180deg,
		#dc2430 0%,
		#7b4397 100%
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.Tilt img {
	max-height: 144px;
}

.form {
	width: 700px;

	background: -webkit-radial-gradient(0% 50%, circle, rgba(96, 16, 48, 0) 9px, #9fe3f9 10px, rgba(96, 16, 48, 0) 11px) 0px
			10px,
		-webkit-radial-gradient(at 100% 100%, rgba(96, 16, 48, 0) 9px, #9fe3f9 10px, rgba(96, 16, 48, 0) 11px), #9d51c4;

	background: radial-gradient(circle at 0% 50%, rgba(96, 16, 48, 0) 9px, #9fe3f9 10px, rgba(96, 16, 48, 0) 11px) 0px
			10px,
		radial-gradient(at 100% 100%, rgba(96, 16, 48, 0) 9px, #9fe3f9 10px, rgba(96, 16, 48, 0) 11px), #9d51c4;
	background-size: 20px 20px;
}

.form button {
	background-color: #e82259;
}

.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}

.App {
	text-align: center;
}

.center {
	display: flex;
	justify-content: center;
}

.particles {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
}

