.Tilt {
	background: #7b4397; /* fallback for old browsers */
	background: -webkit-linear-gradient(180deg, #dc2430 0%, #7b4397 100%); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		180deg,
		#dc2430 0%,
		#7b4397 100%
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.Tilt img {
	max-height: 144px;
}
